#Map {
    text-align: right;
}
#Map>svg {
    max-width: 540px;
    max-height: 316px;
    margin-top: 50px;
}

#Map>svg>text {
    font-weight: bold;
    font-size: 11px;
    user-select: none;
}

#Map>svg polygon {
    opacity: 0;
}

#Map>h4 {
    margin-inline-start: 15px;
    font-weight: bold;
}

#Map>.info_box {
    min-width: 170px;
    position: absolute;
    left: 40px;
    font-size: 12px;
    opacity: 0.75;
}
#Map>.info_box tr>td:first-child {
    width: 30%;
}